// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App.vue';
import router from './router';
import Axios from 'axios';
import 'normalize.css';
import 'element-ui/lib/theme-chalk/index.css';
import {
    Notification,
    Form,
    FormItem,
    Input,
    Button,
    Row,
    Col,
    Menu,
    MenuItem,
    Container,
    Aside,
    Header,
    Main,
    Footer,
    Loading
} from 'element-ui';

Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(Button);
Vue.use(Row);
Vue.use(Col);
Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(Container);
Vue.use(Aside);
Vue.use(Header);
Vue.use(Main);
Vue.use(Footer);
Vue.use(Loading.directive);

Vue.prototype.$notify = Notification;
Vue.prototype.$http = Axios;


Vue.config.productionTip = false;
Axios.defaults.withCredentials = true; // 设置允许跨域cookie, 否则可能会导致cookie的丢失

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    template: '<App/>',
    components: {App}
});



