<template>
    <el-menu mode="vertical" :collapse="true" :router="true" default-active="1" id="sidebar"
             :style="{height: windowHeight}">
        <el-menu-item index="1" :route="{name: 'home'}">
            <i class="el-icon-star-on"></i>
            <span slot="title">首页</span>
        </el-menu-item>

        <el-menu-item index="2" :route="{name: 'login'}">
            <i class="el-icon-setting"></i>
            <span slot="title">管理</span>
        </el-menu-item>
    </el-menu>
</template>

<script>
    export default {
        computed: {
            windowHeight() {
                return document.body.clientHeight + 'px';
            }
        }
    }
</script>

<style scoped>

</style>



