<template>
    <div class="loginForm" v-loading="fromLoading" @keyup.enter="login">
        <el-form :model="formData" :rules="rules" :labelWidth="lineWidth" ref="loginForm" >
            <!-- prop是用于表单验证的, 其和rules中的属性名对应 -->
            <el-form-item label="用户名" prop="username">
                <el-input v-model="formData.username" placeholder="请输入6-18位的用户名"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input v-model="formData.password" placeholder="请输入6-25位的密码" type="password"></el-input>
            </el-form-item>
            <el-form-item label="验证码" prop="captcha">
                <el-input v-model="formData.captcha" :maxlength="4" class="captcha" v-loading="captchaLoading">
                    <template slot="append" >
                        <img :src="imgBase64Formatted" alt="验证码" @click="getCaptcha" class="captchaImg"/>
                    </template>
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="login" >
                    登录
                </el-button>
                <el-button @click="register">
                    注册
                </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    let loginUrl = 'http://localhost:8080/account/login';

    export default {
        // 用get获取验证码, 同时让服务端设置sessionID.
        created() {
            this.getCaptcha();
        },

        data() {
            return {
                lineWidth: '80px',
                captchaLoading: true,
                fromLoading: false,
                // 表单数据
                formData: {
                    username: null,
                    password: null,
                    captcha: null // 验证码
                },
                // 表单验证规则
                rules: {
                    username: [
                        {required: true, message: '请输入用户名', trigger: 'blur'},
                        {min: 6, max: 18, message: '长度在 6 到 18 个字符', trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                        {min: 6, max: 25, message: '长度在 6 到 25 个字符', trigger: 'blur'}
                    ],
                    captcha: [
                        {required: true, message: '请输入验证码', trigger: 'blur'},
                        {len: 4, message: '验证码必须是4位的', trigger: 'blur'}
                    ]
                },
                // 验证码图片数据, 使用base64的编码
                imgBase64: ''
            };
        },

        computed: {
            imgBase64Formatted() {
                return `data:image/jpg;base64,${this.imgBase64}`;
            }
        },

        methods: {
            getCaptcha() {
                this.captchaLoading = true;
                this.$http.get(loginUrl)
                    .then(response => {
                        this.imgBase64 = response.data.imgBase64;
                        this.captchaLoading = false;
                    })
            },
            login() {
                this.$refs["loginForm"].validate((valid) => {
                    if (valid) {
                        this.fromLoading = true;
                        this.$http.post(loginUrl, {
                            username: this.formData.username,
                            password: this.formData.password,
                            captcha: this.formData.captcha
                        }).then(response => {
                            if (response.data.status) {
                                this.$notify.success({
                                    title: '登录成功',
                                    message: `欢迎您: ${this.formData.username}`,
                                    duration: 4000,
                                });
                            } else {
                                this.$notify.warning({
                                    title: '登录失败',
                                    message: response.data.message,
                                    duration: 4000,
                                });
                                this.formData.captcha = null;
                                this.getCaptcha();
                            }
                            this.fromLoading = false;
                        }).catch(error => {
                            this.$notify.error({
                                title: '服务器异常',
                                message: '无法连接到服务器',
                                duration: 4000,
                            });
                            this.fromLoading = false;
                        });
                    } else {
                        this.$notify.warning({
                            title: '登录失败',
                            message: "请正确地填写表单信息",
                            duration: 4000,
                        });
                    }
                });

            },
            register() {
                this.$notify.error({
                    title: '抱歉',
                    message: '本站暂不开放注册',
                    duration: 4000,
                })
            }
        }

    }
</script>

<style scoped>
    .loginForm {
        float: left;
        margin-top: 150px;
        margin-left: 35%;
        width: 30%;
        border: 1px outset #7a7f8a;
        padding: 35px 35px 20px 20px;
    }

    .captcha div {
        padding: 5px;
    }

    .captchaImg {
        width: 100px;
        height: 24px;
    }
</style>



